import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import BaseTemplate from '../components/BaseTemplate';
import sa from '../services/superagent';
import INIT_URL from '../atoms/setting';
import "../components/scrollbar.css";

function Home(){
    const [state, setState] = useState({})
    let history = useHistory()
    useEffect(()=>{
        sa.get( (localStorage.host_url ?? INIT_URL)+"/data/stat.json").then((response)=>{
            if (response.ok){
                setState(response.body)
            }else{
                setState(false)
            }
        })
        document.getElementsByTagName('html')[0].classList.add("overflow-x-hidden")
        return () => {
            document.getElementsByTagName('html')[0].classList.remove("overflow-x-hidden")
        }
    }, []);

    function content(data){
        
        if (data === false){
            return <></>
        }else if (Object.keys(state).length < 1){
            return <></>
        }else{

            return (
                <section className="dark:text-gray-400 dark:bg-gray-900 text-gray-600 body-font">
                <div className="container px-5 py-5 mx-auto flex flex-col">
                  <div className="lg:w-4/6 w-full mx-auto">
                    <h1 className="leading-relaxed text-2xl strong mb-4 dark:text-white text-black">My Spotify Playlists</h1>
                    <div className="rounded-lg h-64 w-full no-scrollbar overflow-x-scroll gap-6 grid grid-flow-col grid-row-1">
                    {
                        (data.playlists ?? []).map((entry,ind)=>(
                            (entry.type ?? "spotify") === "spotify" ? 
                            <iframe key={"playlist_"+ind.toString()} title={"playlist_"+ind.toString()} src={entry['url'] ?? ""} width="380" height="100%" frameBorder="0" allowtransparency="true" allow="encrypted-media"></iframe> 
                            :
                            <div key={"playlist_"+ind.toString()} onClick={()=>{window.open(entry['url'] ?? "", "_blank")}} className="w-80 mx-auto overflow-hidden bg-white rounded-lg shadow-lg dark:bg-gray-800">
                                <img className="object-cover w-48 h-48 mt-2" src={entry['image'] ?? ""} alt="Playlist"/>
                                <div className="flex items-center justify-between px-4 py-2 bg-gray-900">
                                    <h1 className="text-lg font-bold text-white">{entry['title'] ?? ""}</h1>
                                </div>
                            </div>
                        ))
                    }

                    </div>
                    <div className="flex flex-col sm:flex-row mt-10">
                      <div className="sm:w-1/3 text-center sm:pr-8 sm:py-8">
                        <div className="flex flex-col items-center text-center justify-center">
                          <h2 className="font-medium title-font mt-4 dark:text-white text-black text-lg">Latest Stats:</h2>
                          <div className="w-12 h-1 bg-indigo-500 rounded mt-2 mb-4"></div>
                          <div className="flex flex-col">
                              {
                                  (data.stats ?? []).map((entry,ind)=>(
                                    <div key={"stat_"+ind.toString()} className="flex flex-row items-center">
                                        <p className="text-base dark:text-gray-400 text-gray-700 pr-3">{entry['stat']}</p> 
                                        {(entry['type'] ?? "value")=== "color" ? 
                                            <div className={"w-4 h-4 rounded-full inline-flex items-center justify-center " + (entry['value'] === "green" ? "bg-green-600" : entry['value'] === "red" ? "bg-red-600" : "bg-yellow-400" )}/> :
                                            <p>{entry['value']}</p>
                                        }
                                        
                                    </div>
                                  ))
                              }
                          </div>
                          
                        </div>
                      </div>
                      <div className="sm:w-2/3 sm:pl-8 sm:py-8 sm:border-l border-gray-800 sm:border-t-0 border-t mt-4 pt-4 sm:mt-0 text-center sm:text-left">
                      <div className="-my-8 divide-y-2 divide-gray-100 dark:divide-white">
                      <h1 className="leading-relaxed text-2xl strong mb-4 dark:text-white text-black">Latest/Pinned posts</h1>
                      {(data.pinned_posts ?? []).map((entry, ind)=>(
                          <div key={"pinned_"+ind.toString()}>
                          <h2 className="leading-relaxed text-3xl mb-4">{entry.title}</h2>
                          <p className="leading-relaxed text-lg mb-4">{entry.summary}</p>
                          <span onClick={()=>history.push('/post/'+entry.link)} className="cursor-pointer pb-3 text-indigo-400 inline-flex items-center">Read More
                            <svg fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" className="w-4 h-4 ml-2" viewBox="0 0 24 24">
                              <path d="M5 12h14M12 5l7 7-7 7"></path>
                            </svg>
                          </span>
                          </div>

                        )) 
                        }
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </section>
            )
        }
    }

    return(
        <>
        <BaseTemplate children={content(state)}/>
        </>
    )
}

export default Home;

