import React, { useEffect, useState } from 'react';
import BaseTemplate from '../components/BaseTemplate';
import { Link, Redirect, useHistory } from 'react-router-dom';
import sa from '../services/superagent';
import INIT_URL from '../atoms/setting';

function Archive(props){
    const [archive, setState] = useState(null)
    let history = useHistory();
    const URL_ENDPOINT = "/data/archives/archive_"
    const URL_SUFFIX = ".json"
    useEffect(()=>{
        let page = props.match.params.page ?? 0
        sa.get((localStorage.host_url ?? INIT_URL)+URL_ENDPOINT + page.toString() + URL_SUFFIX).then((response)=>{
            if (response.ok){
                setState(response.body)

            }else{
                setState(false)
            }
        })
    }, [props.match.params.page]); 

    function getYear(date){
        return (new Date(date)).getFullYear() || "YYYY"
    }

    function getDate(date){
        const months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]
        let month = (new Date(date)).getMonth()
        let day = (new Date(date)).getDate()
        return ((months[month] || "MMM")+", "+( day || "DD") )
    }

    function children(data = null){
        if (data === false){
            return <Redirect to="/404" />
        }else{
        return(
        <div>
        <h1 className="pt-5 text-center text-2xl font-bold">Archive</h1>
        <section className="dark:text-gray-400 dark:bg-gray-900 text-gray-600 body-font overflow-hidden">
        <div className="container px-5 py-24 mx-auto">
            <div className="-my-8 divide-y-2 divide-gray-100 dark:divide-white">
            {data == null ? <></>:
            (data.data ?? []).map((entry, ind)=>(
                <div key={"entry_"+ind.toString()} className="py-8 flex flex-col md:flex-row flex-wrap lg:flex-nowrap">
                <div className="md:w-64 md:mb-0 mb-6 flex-shrink-0 flex flex-col">
                <span className="font-semibold title-font text-gray-700 dark:text-white">{getYear(entry.lastUpdated) || ""}</span>
                <span className="mt-1 text-gray-500 text-sm">{getDate(entry.lastUpdated) || ""}</span>
                </div>
                <div className="md:flex-grow">
                <h2 className="text-2xl font-medium text-gray-900 dark:text-white title-font mb-2">{entry.title}</h2>
                <p className="leading-relaxed">{entry.summary}</p>
                <span onClick={()=>history.push('/post/'+entry.link)} className="cursor-pointer text-indigo-500 inline-flex items-center mt-4">Read More
                    <svg className="w-4 h-4 ml-2" viewBox="0 0 24 24" stroke="currentColor" strokeWidth="2" fill="none" strokeLinecap="round" strokeLinejoin="round">
                    <path d="M5 12h14"></path>
                    <path d="M12 5l7 7-7 7"></path>
                    </svg>
                </span>
                </div>
                </div>
            ))
            }
            </div>
        </div>
        </section>

        {data === null ?
            <div className="no-scrollbar overflow-y-auto w-full grid grid-flow-col grid-row-1">
            <div className="w-full gap-3 p-1 flex flex-none justify-center">
            <span className="flex-none items-center px-4 py-2 mx-1 bg-white rounded-md dark:bg-gray-800 text-gray-500 cursor-not-allowed dark:text-gray-600">Previous</span>
            <span className="flex-none items-center px-4 py-2 mx-1 bg-white rounded-md dark:bg-gray-800 text-gray-500 cursor-not-allowed dark:text-gray-600">Next</span>
            </div></div>
        :
            <div className="no-scrollbar overflow-y-auto w-full grid grid-flow-col grid-row-1">
            <div className="w-full gap-3 p-1 flex flex-none justify-center">
            {data.prev === null ? 
            <span className="flex-none items-center px-4 py-2 mx-1 bg-white rounded-md dark:bg-gray-800 text-gray-500 cursor-not-allowed dark:text-gray-600">Previous</span> : 
            <Link to={'/archive/' + data.prev} className="flex-none items-center px-4 py-2 mx-1 bg-white rounded-md dark:bg-gray-800 text-gray-700 dark:text-gray-400 transition-colors duration-200 transform hover:bg-indigo-600 dark:hover:bg-indigo-500 hover:text-white dark:hover:text-gray-100">Previous</Link>
            }
            {data.next === null ? 
            <span className="flex-none items-center px-4 py-2 mx-1 bg-white rounded-md dark:bg-gray-800 text-gray-500 cursor-not-allowed dark:text-gray-600">Next</span> :
            <Link to={'/archive/' + data.next} className="flex-none items-center px-4 py-2 mx-1 bg-white rounded-md dark:bg-gray-800 text-gray-700 dark:text-gray-400 transition-colors duration-200 transform hover:bg-indigo-600 dark:hover:bg-indigo-500 hover:text-white dark:hover:text-gray-100">Next</Link>
            }
            </div>
            </div>
        }
        
        </div>
        )
        }
    } 
    

    return(
        <>
        <BaseTemplate children={children(archive)}/>
        
        </>
    )
}

export default Archive;

